
* {
  box-sizing:border-box;
  margin:0;
  padding:0;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
p {
  font-size: 1.6rem;
}
h1 {
  font-size: 6rem;
}

.weather {
  width: 100%;
  height:100vh;
  position:relative;

  color: #fff
}
.weather:before {
  content: '';
  background: url('https://wallpapers.com/images/hd/scenic-gradient-seascape-v5rhwlvmmkakoeeg.jpg') no-repeat center center/cover;
  position:absolute;
  width:100%;
  height:100%;
  top:0;
  left:0;
 z-index:-1;
}

.weather .search {
  text-align:center;
  padding:1rem;
}

.weather input {
  padding:.7rem 1.5rem;
  font-size:1.2rem;
  border-radius:25px;
  border: 1px solid rgba(255,255,255,0.8);
  background: rgba(255,255,255,0.1);
  color:#f8f8f8;
}

::placeholder {
  color: #f8f8f8;
}

.container {
  max-width:700px;
  height:700px;
  margin:auto;
  padding: 0 1rem;
  position: relative;
  top: 10%;
  display:flex;
  flex-direction: column;
  justify-content: space-between;
}

.weather .top {
  width: 100%;
  margin: 1rem auto;
}

.weather .description {
  position: relative;

  transform-origin:0 0;

}
.weather .time {
  position: relative;
  right:-80%;
 
}

.weather .bottom {
  display:flex;
  justify-content:space-evenly;
  text-align:center;
  width:100%;
  margin:1rem auto;
  padding:1rem;
  border-radius:12px;
  background-color:rgba(255,255,255,0.2);
}

.bold{
  font-weight:700;
}

.btn-style {
  margin-left:20px;
  padding:.7rem 1rem;
  font-size:1.2rem;
  border-radius:25px;
  border: 1px solid rgba(255,255,255,0.8);
  background: rgba(204, 10, 33, 0.1);
  color:#f8f8f8;
}

.btn-style:hover {
  border:1px solid rgba(255,255,255,0.8);

}

.card-container {
  display:flex;
  width:100%;
  
  justify-content: center;
  margin-top:100px;
}

.card {
  background-color: #827f7f;
  color:#f8f8f8
}